(function() {
  'use strict';

  angular
    .module('sortedBudgetingTool')
    .service('profileApi', profileApi);

  /** @ngInject */
  function profileApi($log,
               $cookies,
               $q,
               $http,
               $location,
               siteConfig) {

    // var DOMAIN = ($location.host() == 'localhost') ? 'http://sorted.local' : '' ;

    var DOMAIN = setDomain();

    var BASE_URL = DOMAIN + siteConfig.API_PREFIX + '/profile/';

    var profile;


    function getProfile() {
      return profile
    }

    function setDomain()
    {
      var domain;
      console.log($location.host());
      switch($location.host()) {
        case 'localhost' :
          // domain = 'http://sorted.local/';
          domain = 'http://localhost';
          break;

        // case '192.168.178.47' :
        //   domain = 'http://192.168.33.10/';
        //   break;

        default :
          domain = '';
          break;
      }

      return domain;
    }

    function get () {


      console.log(BASE_URL + 'get');

      return $http({
        method: 'GET',
        url: BASE_URL + 'get'
      })
        .then(function (response) {
          if (response.data.success === 'false') {
            return $q.reject();
          }
          console.log(profile);
          profile = response.data;
          return response.data;
        }, function (error) {
          return $q.reject(error);
        });
    }

    function isLoggedIn () {
      return get()
        .then(function () {
          return true;
        })
        .catch(function () {
          return false;
        });
    }

    function getUploadURL() {
      return BASE_URL + 'custom';
    }

    function uploadBudgetImage(image) {
      var method = 'POST';
      var url = BASE_URL + 'custom';

      // NB: IE 10+ only
      // TODO: fallback option for the diehards still using IE 9
      var formData = new FormData();
      formData.append('Image', image);
      return $http({
        data: formData,
        method: method,
        url: url,
        transformRequest: angular.identity,
        headers: {'Content-Type': undefined}
      })
        .then(function (response) {
          return response.data;
        }, function () {
          return null;
        });
    }

    return {
      get: get,
      uploadBudgetImage: uploadBudgetImage,
      isLoggedIn: isLoggedIn,
      getUploadURL: getUploadURL,
      getProfile: getProfile,
    };
  }
})();

(function() {
  'use strict';

  angular
    .module('sortedBudgetingTool')
    .service('Api', Api);

  /** @ngInject */
  function Api($log,
               $cookies,
               $q,
               $http,
               $uibModal,
               $location,
               BudgetCalculator,
               toastr) {

    var DOMAIN = setDomain();

    var hasAccessedTool = false;

    var budgetData;
    var lastEditedDate;
    var budgetId;
    var isNewBudget = true;

    function getLastEditDate () {
      return lastEditedDate;
    }

    function getIsNewBudget () {
      return isNewBudget;
    }

    function setIsNewBudget (isNew) {
      isNewBudget = isNew;
    }

    function getBudgetData () {
      return budgetData;
    }

    function setDomain()
    {
      var domain;
      console.log($location.host());
      switch($location.host()) {
        case 'localhost' :
          // domain = 'http://sorted.local/';
          domain = 'http://localhost/';
          break;

        // case '192.168.178.47' :
        //   domain = 'http://192.168.33.10/';
        //   break;

        default :
          domain = '';
          break;
      }

      return domain;
    }


    function loadBudget() {
      console.log('Api : loadBudget');
      var request = DOMAIN + 'api/v0.1/calculator/get/2';

      return $http.get(request).then(function (resp) {
        var budgetsArr = resp.data;

        console.log("HAS DATA", budgetsArr);
        if(budgetsArr.length > 0){
          try {

            // try to convert the old format to an obj
            budgetData = angular.copy(JSON.parse(budgetsArr[budgetsArr.length - 1].Data));
          }catch (e) {
            //we threw an error must be an object
            budgetData = angular.copy(budgetsArr[budgetsArr.length - 1].Data);
          }
          lastEditedDate = new Date.parse(budgetsArr[budgetsArr.length - 1].LastEdited)
          budgetId = budgetsArr[budgetsArr.length - 1].ID;
          isNewBudget = false;
          console.log(budgetsArr[budgetsArr.length - 1]);
          console.log(budgetData);
          console.log(lastEditedDate);
        }

        return budgetData;

      });

    }

    function saveBudget(parsedObj, forceSave) {

      if((getBudgetData() && getIsNewBudget())) {

        if(forceSave) {
          //console.log('Forced to save')
        } else {

          showWarning();
          return
        }

      }
      var request;
      if(getIsNewBudget() && getBudgetData()) {

        //console.log("is update");
        request = DOMAIN + 'api/v0.1/calculator/update/' + budgetId;

      }else if (getIsNewBudget()) {

        //console.log("is Save");
        request = DOMAIN + 'api/v0.1/calculator/save';


      } else {

        //console.log("is update");
        request = DOMAIN + 'api/v0.1/calculator/update/' + budgetId;

      }


      var cleanObj = angular.copy(parsedObj);

      var sendData = {
        Data: cleanObj,
        CalcID: 2
      };

      BudgetCalculator.current.changeSinceSave = false;


      $http.post(request,  sendData, {
        headers: {'Content-Type': 'application/json'}
      }).then(function (resp) {

        toastr.success('Budget saved successfully');

        isNewBudget = false;
        var budgetsArr = resp.data;
        if(budgetsArr.status == 'success'){
          budgetId = budgetsArr.data;
        }
        return true;
      }).catch(function (err) {
        toastr.error('Please check your login and try again', 'Budget not saved!');
        return false;
      });
    }

    function showWarning () {

      var modalInstance = $uibModal.open({
        templateUrl: 'app/components/modals/multiBudgetModal.html',
        controller: 'MultiBudgetModalController',
        controllerAs: '$ctrl',
        size: 'lg'
      });

    }

    function get_SiteData()
    {
      var request = DOMAIN+'themes/sorted/json/budget-tool-v2/budget-tool-data.json';

      return $http({
        url: request,
        headers: {'Content-Type': 'application/json'}});
    }



    var service = {
      get_SiteData : get_SiteData,
      getIsNewBudget: getIsNewBudget,
      getLastEditDate: getLastEditDate,
      setIsNewBudget: setIsNewBudget,
      saveBudget : saveBudget,
      loadBudget: loadBudget,
      getBudgetData: getBudgetData,
      hasAccessedTool: hasAccessedTool

    };

    return service;
  }
})();

(function() {
  'use strict';

  angular
    .module('sortedBudgetingTool')
    .directive('restrictCurrency', restrictCurrency);

  /** @ngInject */
  function restrictCurrency() {
    var directive = {
      restrict: 'A',
      link: linkFunc
    };

    return directive;

    function linkFunc(scope, el, attr) {

      function on_keyPressHandler(ev)
      {

        if (ev.defaultPrevented) {
          return; // Should do nothing if the default action has been cancelled
        }
        var key;
        var handled = false;
        if (ev.key !== undefined) {
          // Handle the event with KeyboardEvent.key and set handled true.
          key = ev.which;
        } else if (ev.keyIdentifier !== undefined) {
          // Handle the event with KeyboardEvent.keyIdentifier and set handled true.
          key = ev.keyIdentifier;
        } else if (ev.keyCode !== undefined) {
          // Handle the event with KeyboardEvent.keyCode and set handled true.
          key = ev.keyCode;
        }

        if (handled) {
          // Suppress "double action" if event handled
          ev.preventDefault();
        }

        // console.log(key);

        if(key == 13) // On Enter key
        {
          ev.preventDefault();

          angular.element(ev.currentTarget).blur();  // Force focus out

          return;
        }

        if(key == 49 || key == 50 || key == 51 || key == 52 || key == 53 || key == 54 || key == 55 || key == 56 || key == 57 || key == 48 || key == 44 || key == 46 || key == 8 || key == 0) {

          // Allow 1,2,3,4,5,6,7,8,9,0,.,, DELETE, DELETE

        } else {
          ev.preventDefault();
          return;
        }
      }

      function on_keyUpHandler(ev)
      {
        // Don't allow too many characters after the decimal
        // var parseString = angular.element(ev.currentTarget).val();
        // if(parseString.split('.')[1] && (parseString.split('.')[1].length > 2)) {
        //   angular.element(ev.currentTarget).val(parseString.slice(0, -1));
        // }
      }

      function on_blurHandler(ev)
      {
        var str = this.value;
        // if(str.toString().length <= 0) {
        //   str = '0';
        // }
        // console.log(str)

        str = str.replace(/,/g, '');
        var strToFloat = parseFloat(str).toFixed(2);

        if(isNaN(strToFloat)) {
          this.value = '0.00';
        } else {
          this.value = strToFloat.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,")
          // this.value = strToFloat.toString();
        }

        var $input = $(this);
        $input.trigger('input');
        $input.trigger('change');
      }


      function on_focusHandler(ev)
      {
        var str = this.value;
        if((parseFloat(str) <= 0) || isNaN(parseFloat(str))) {
          this.value = '';
        }
      }

      el.on('keypress', on_keyPressHandler);
      el.on('keyup', on_keyUpHandler);
      el.on('blur', on_blurHandler);
      el.on('focus', on_focusHandler);

      scope.$on('$destroy', function () {

      });
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('sortedBudgetingTool')
    .directive('preventInputZoom', preventInputZoom);

  /** @ngInject */
  function preventInputZoom() {
    var directive = {
      link: linkFunc
    };

    return directive;

    function linkFunc(scope, el, attr) {

      function on_focusHandler(ev)
      {
        this.className += ' font-prevent-zoom';
        this.classList.remove('font-prevent-zoom');
      }

      el.on('focus', on_focusHandler);

      scope.$on('$destroy', function () {

      });
    }

  }

})();

